import React from 'react'

const StructuredCabling = () => {
  return (
    <div>
        
        <h2>OKABLOWANIE STRUKTURALNE:</h2>

    <span>Okablowanie strukturalne tworzy infrastruktur teleinformatyczn, a wic swego rodzaju krwioobieg sieci w danym budynku. To wanie dziki kablom moliwa jest komunikacja midzy poszczeglnymi urzdzeniami, dlatego ich jako, rozoenie oraz atwy dostp jest jednym z najwaniejszych elementw, ktre decyduj o niezawodnoci caej sieci. Dziki zastosowaniu okablowania strukturalnego, moliwe jest bardzo proste podczenie urzdze do sieci w dowolnym miejscu budynku. W ramach kadej sieci moemy wyrni dwie grupy okablowania strukturalnego: okablowanie poziome i okablowanie pionowe. Dodatkowo w caej strukturze wystpuj te punkty dystrybucyjne czsto nazywane dostpowymi.
    </span>
    </div>
  )
}

export default StructuredCabling